.palette__color {
  border-radius: 50%;
  position: relative;
}
.palette__line {
  height: 10px;
}
.palette--small {
  width: 2rem;
  height: 2rem;
}
.palette--big {
  width: 4.75rem;
  height: 4.75rem;
  box-shadow: 0px 12px 24px rgba(0, 19, 36, 0.25);
}
.palette--big:not(:last-child) {
  margin-right: -1.25rem;
}
.palette--vervendo-white {
  background-color: #ffffff;
  z-index: 5;
}
.palette--vervendo-blue {
  background-color: #2c6da9;
  z-index: 4;
}
.palette--vervendo-pink {
  background-color: #ff577f;
  z-index: 3;
}
.palette--vervendo-yellow {
  background-color: #ffca41;
  z-index: 2;
}
.palette--vervendo-gold {
  background-color: #c98f1d;
  z-index: 1;
}
.palette--small + .palette--vervendo-pink::after, .palette--small + .palette--vervendo-yellow::after, .palette--small + .palette--vervendo-gold::after {
  content: "new";
  position: absolute;
  right: -0.875rem;
  top: 0.375rem;
  border: 0.1875rem solid var(--blue-100);
  background-color: #fff;
  color: var(--blue-800);
  font-size: 0.5625rem;
  font-weight: 600;
  border-radius: 100vh;
  padding: 0.0625rem 0.25rem;
}

.color-value {
  color: #fff;
  padding: 1.875rem;
  max-width: 52ch;
}
.color-value__line {
  width: 0.625rem;
}
.color-value__hex {
  font-size: 0.875rem;
}
.color-value__heading {
  font-size: 0.6875rem;
  color: var(--blue-400);
}
.color-value__body {
  font-size: 0.6875rem;
  font-weight: 200;
  color: var(--blue-200);
  letter-spacing: 0.01875rem;
  line-height: 1.3;
}

.mockup {
  width: 52ch;
}
@media (max-width: 585px) {
  .mockup {
    width: 350px;
  }
}
@media (max-width: 377px) {
  .mockup {
    width: 300px;
  }
}
.footer__social {
  padding: 5.625rem 0 1.875rem;
}
.footer__credit {
  padding-bottom: 1.875rem;
  font-size: 0.7rem;
  color: var(--blue-200);
  text-align: center;
  font-weight: 300;
  letter-spacing: 0.03125rem;
}
.footer .--yellow-text {
  color: var(--yellow-500);
}